import { Suspense, lazy, useCallback, useEffect, useState } from "react";
import { assestsNavbarUrl } from "../../Services/UrlConfig/AssestsBaseUrl";
import CreateButton from "../../Ui/Button/CreateButton";
import BorderBottom from "../../Ui/Border/BorderBottom";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import UserMemberContainerWrapper from "../../Ui/UserMember/UserMemberContainerWrapper";
import QvipleLoader from "../../Loader/QvipleLoader";
import Loading from "../../Loader/Loading";
import EmptyMessage from "../../Utils/EmptyMessage/EmptyMessage";
import { useInView } from "react-intersection-observer";
import { nextPage } from "../../Utils/Functions/nextPage";
import { customDebounce } from "../../Utils/Functions/debounce";
import { useTranslation } from "react-i18next";
import style from "../../UserStaffMember/FinanceManager/FinanceManager.module.css";
import { useCommitteeAuthorityList } from "../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddCommitteeAuthority from "./AddCommitteeAuthority";
import InstituteCommitteeAutorityCard from "./InstituteCommitteeAutorityCard";
const InstituteCommitteeAutority = ({ qid }) => {
  const { t } = useTranslation();
  const [search, setSearch] = useState("");
  const [page, setPage] = useState(1);
  const [state, setState] = useState(true);
  const [ref, inView] = useInView({ skip: search });
  const [add, setAdd] = useState(false);
  const {
    committeeAuthorityList,
    committeeAuthorityListLoading,
    committeeAuthorityListRefetch,
  } = useCommitteeAuthorityList({
    data: {
      qid: qid,
      page: page,
      limit: 10,
      search: search,
    },
    skip: !qid,
  });
  useEffect(() => {
    if (inView && state) setPage((page) => page + 1);
  }, [inView, state]);
  useEffect(() => {
    if (committeeAuthorityList?.list?.length) {
      let obj = nextPage(committeeAuthorityList?.list?.length);
      setState(() => obj);
    }
  }, [committeeAuthorityList?.list]);

  const onDebounce = useCallback(
    customDebounce((val) => {
      setPage(() => 1);
      setSearch(val);
    }, 1500),
    []
  );

  const onSearchEvent = (e) => {
    if (e.target.value) onDebounce(e.target.value);
    else {
      setPage(() => 1);
      setSearch("");
    }
  };

  const onRefetch = useCallback(() => {
    setPage(() => 1);
    setSearch("");
    committeeAuthorityListRefetch();
  }, [committeeAuthorityListRefetch]);

  const onToggle = () => {
    setAdd((pre) => !pre);
  };
  return (
    <>
      <UserMemberContainerWrapper>
        <UserMemberContentWrapper>
          <div className={style.fm_header_container}>
            <h6>{t("committee")}</h6>
            <div className={style.fm_search}>
              <section className={style.fm_search_container}>
                <div className={style.fm_search_container_inner}>
                  <img
                    className={style.fm_search_icon}
                    alt="search icon"
                    src={`${assestsNavbarUrl}/navbar-search.svg`}
                  />
                  <input
                    type="text"
                    placeholder={t("search")}
                    onChange={onSearchEvent}
                  />
                </div>
              </section>
              <CreateButton
                label={t("add_committee")}
                customStyle={{
                  margin: "0",
                  padding: "0.4rem 1rem",
                }}
                onAction={onToggle}
              />
            </div>
          </div>
        </UserMemberContentWrapper>
        <BorderBottom />
        <UserMemberContentWrapper>
          {committeeAuthorityList?.list?.map((committeeAuthority, index) =>
            committeeAuthorityList?.list?.length === index + 1 ? (
              <div key={committeeAuthority?._id} ref={ref}>
                <InstituteCommitteeAutorityCard
                  committeeAuthority={committeeAuthority}
                  onRefetch={onRefetch}
                  qid={qid}
                />
              </div>
            ) : (
              <InstituteCommitteeAutorityCard
                committeeAuthority={committeeAuthority}
                key={committeeAuthority?._id}
                onRefetch={onRefetch}
                qid={qid}
              />
            )
          )}
          {committeeAuthorityListLoading ? (
            search ? (
              <QvipleLoader />
            ) : (
              <Loading />
            )
          ) : search ? (
            committeeAuthorityList?.list?.length === 0 && (
              <EmptyMessage title="No authority found related this search." />
            )
          ) : (
            committeeAuthorityList?.list?.length === 0 && (
              <EmptyMessage title="No authority." />
            )
          )}
        </UserMemberContentWrapper>
      </UserMemberContainerWrapper>
      {add && (
        <Suspense fallback={<QvipleLoader />}>
          <AddCommitteeAuthority
            onClose={onToggle}
            onRefetch={onRefetch}
            qid={qid}
          />
        </Suspense>
      )}
    </>
  );
};

export default InstituteCommitteeAutority;
