import style from "../InstituteFeedback/InstituteFeedback.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import InstituteCommitteeAutorityCardMenu from "./InstituteCommitteeAutorityCardMenu";
import { useState } from "react";
const InstituteCommitteeAutorityCard = ({
  committeeAuthority,
  onRefetch,
  qid,
}) => {
  const [step, setStep] = useState("");

  const onClose = () => {
    setStep("");
  };
  const onMenu = () => {
    setStep(1);
  };
  return (
    <>
      <div className={style.sf_card}>
        <div className={style.sf_card_contatiner}>
          <img
            src={
              committeeAuthority?.custom_head_person?.staffProfilePhoto
                ? `${imageShowUrl}/${committeeAuthority.custom_head_person?.staffProfilePhoto}`
                : `${assestsFeedUrl}/feed-user-avatar.svg`
            }
            loading="lazy"
            alt="avatar"
          />

          <div className={style.sf_card_contatiner_inner}>
            <h6>
              {committeeAuthority?.custom_head_name
                ? committeeAuthority?.custom_head_name === "RND"
                  ? "R&D"
                  : committeeAuthority?.custom_head_name
                : ""}
            </h6>
            <p>
              {`${
                committeeAuthority.custom_head_person?.staffFirstName ?? ""
              } ${
                committeeAuthority.custom_head_person?.staffMiddleName ?? ""
              } ${committeeAuthority.custom_head_person?.staffLastName ?? ""}`}
            </p>
          </div>
        </div>
        <img
          src={`${assestsUserFinanceUrl}/menu.svg`}
          alt="menu icon"
          onClick={onMenu}
        />
      </div>
      {step === 1 ? (
        <InstituteCommitteeAutorityCardMenu
          onClose={onClose}
          onRefetch={onRefetch}
          menuData={committeeAuthority}
          qid={qid}
        />
      ) : null}
    </>
  );
};

export default InstituteCommitteeAutorityCard;
