import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import {
  existingRequiredField,
  requiredField,
} from "../../Validation/functions/checkingRequiredField";
import Modal from "../../Ui/Modal/Modal";
import UserMemberContentWrapper from "../../Ui/UserMember/UserMemberContentWrapper";
import BorderBottom from "../../Ui/Border/BorderBottom";
import style from "../../UserStaffMember/FinanceManager/Income/Income.module.css";
import {
  assestsFeedUrl,
  assestsUserFinanceUrl,
} from "../../Services/UrlConfig/AssestsBaseUrl";
import QvipleLoader from "../../Loader/QvipleLoader";
import CreateButton from "../../Ui/Button/CreateButton";
import TileCard from "../../Ui/Tab/Tile/TileCard";
import TileWrapper from "../../Ui/Tab/Tile/TileWrapper";
import { imageShowUrl } from "../../Services/UrlConfig/BaseUrl";
import styleEdit from "../../../Profile/Institute/Pages/Site/SiteEdit.module.css";
import AssignStaffModal from "../../../Department/Institute/AssignStaff/AssignStaffModal";
import { useCommitteeAddAuthority } from "../../Hooks/ApiHooks/SiteManager/site-manager-api";
import FormInput from "../../Ui/Input/FormInput";

const AddCommitteeAuthority = ({
  onClose,
  qid,
  onRefetch,
  openAs,
  menuData,
  onEdit,
}) => {
  const { t } = useTranslation();
  const [state, setState] = useState({
    custom_head_name: "IQAC",
    custom_title_person: "",
    custom_head_person: "",
  });
  const [errorField, setErrorField] = useState("");
  const [disabled, setDisabled] = useState(false);
  const [assignHead, setAssignHead] = useState(false);
  const [assignHeadStaff, setAssignHeadStaff] = useState("");

  const [committeeAddAuthority] = useCommitteeAddAuthority();

  useEffect(() => {
    if (openAs === "EDIT") {
      setState((prev) => ({
        ...prev,
        custom_head_name: menuData?.custom_head_name ?? "",
        custom_title_person: menuData?.custom_title_person ?? "",
        custom_head_person: menuData?.custom_head_person?._id ?? "",
      }));
      setAssignHeadStaff(menuData?.custom_head_person);
    }
  }, [openAs, menuData]);

  const onSelectLeading = (assing) => {
    setAssignHeadStaff(assing);
    setState((prev) => ({
      ...prev,
      custom_head_person: assing?._id,
    }));
  };

  const onChange = (e) => {
    setState((prev) => ({
      ...prev,
      [e.target.name]: e.target.value,
    }));
  };

  const onTile = (value) => {
    // if (value === "Other") {
    // } else {
    if (openAs === "EDIT") {
    } else {
      setState((prev) => ({
        ...prev,
        custom_head_name: value,
      }));
    }

    // }
  };
  const onCreate = () => {
    let validation = null;
    let mdo_obj = null;
    if (!["IQAC", "RND"]?.includes(state.custom_head_name)) {
      validation = requiredField(state, []);
      mdo_obj = { ...state, custom_head_name: state.custom_title_person };
    } else {
      validation = requiredField(state, ["custom_title_person"]);
      mdo_obj = { ...state };
    }
    if (existingRequiredField(validation)) {
      if (openAs === "EDIT") {
        let obj = null;
        if (menuData?.custom_head_person?._id === state.custom_head_person) {
          obj = mdo_obj;
        } else {
          obj = {
            ...mdo_obj,
            o_staff: menuData?.custom_head_person?._id,
            n_staff: state.custom_head_person,
          };
        }
        onEdit(obj);
      } else {
        if (qid) {
          setDisabled((pre) => !pre);
          committeeAddAuthority({
            qid: qid,
            bodyParameter: mdo_obj,
          })
            .then(() => {
              onRefetch();
              setDisabled((pre) => !pre);
              onClose();
            })
            .catch(() => {
              setDisabled((pre) => !pre);
            });
        }
      }
    } else {
      setErrorField(validation);
    }
  };
  // console.info("dsfjhs", errorField);
  return (
    <>
      {assignHead ? (
        <AssignStaffModal
          assignHead={assignHead}
          headSelect={onSelectLeading}
          title={t("select_committee_person")}
          onClose={() => setAssignHead((pre) => !pre)}
        />
      ) : (
        <Modal onClose={onClose}>
          <div className={style.modal_container}>
            <UserMemberContentWrapper>
              <div className={style.modal_container_outer_header}>
                <div className={style.modal_container_header}>
                  <h6>
                    {t(
                      openAs === "EDIT" ? "update_committee" : "add_committee"
                    )}
                  </h6>
                </div>
                <img
                  src={`${assestsUserFinanceUrl}/close.svg`}
                  alt="close icon"
                  onClick={onClose}
                />
              </div>
            </UserMemberContentWrapper>
            <BorderBottom />
            <UserMemberContentWrapper
              customStyle={{
                paddingTop: "0",
              }}
            >
              <TileWrapper label="committee_type">
                {addType?.map((tile) => (
                  <TileCard
                    key={tile?.key}
                    tile={tile}
                    onTile={onTile}
                    activeTile={
                      ["IQAC", "RND"]?.includes(state.custom_head_name)
                        ? state.custom_head_name
                        : "Other"
                    }
                  />
                ))}
              </TileWrapper>
              {state.custom_head_name === "Other" ||
              !["IQAC", "RND"]?.includes(state.custom_head_name) ? (
                <FormInput
                  label={t("committee_name")}
                  placeholder={t("committee_name_placeholder")}
                  name={"custom_title_person"}
                  value={state.custom_title_person}
                  type={"text"}
                  onChange={onChange}
                  errorShow={errorField["custom_title_person"]}
                />
              ) : null}

              <div
                className={styleEdit.leading_person_container}
                onClick={() => setAssignHead(true)}
                style={{
                  marginTop: "1rem",
                  width: "100%",
                }}
              >
                <img
                  src={
                    assignHeadStaff?.staffProfilePhoto
                      ? `${imageShowUrl}/${assignHeadStaff.staffProfilePhoto}`
                      : `${assestsFeedUrl}/feed-user-avatar.svg`
                  }
                  alt="avatar"
                />
                <div className={styleEdit.leading_person_text}>
                  <h6>
                    {assignHeadStaff
                      ? `${assignHeadStaff?.staffFirstName ?? ""} ${
                          assignHeadStaff?.staffMiddleName ?? ""
                        } ${assignHeadStaff?.staffLastName ?? ""}`
                      : t("select_staff")}
                  </h6>
                  <p>
                    {t("search_joined_list")}{" "}
                    {errorField["leading_person"] && (
                      <span style={{ color: "red", marginLeft: "15px" }}>
                        * {t("form_require_label")}
                      </span>
                    )}
                  </p>
                </div>
              </div>

              <CreateButton
                l
                label={openAs === "EDIT" ? "update" : "add"}
                customStyle={{
                  padding: "0.4rem 1rem",
                }}
                onAction={onCreate}
              />
            </UserMemberContentWrapper>
            {disabled && <QvipleLoader />}
          </div>
        </Modal>
      )}
    </>
  );
};

export default AddCommitteeAuthority;

const addType = [
  {
    key: "IQAC",
    name: "iqac",
  },
  {
    key: "RND",
    name: "rnd",
  },
  {
    key: "Other",
    name: "other",
  },
];
