import { useState } from "react";
import { useTranslation } from "react-i18next";
import ModalMenu from "../../Ui/Modal/ModalMenu";
import {
  useCommitteeAuthorityRemove,
  useCommitteeAuthorityUpdate,
} from "../../Hooks/ApiHooks/SiteManager/site-manager-api";
import AddCommitteeAuthority from "./AddCommitteeAuthority";

const InstituteCommitteeAutorityCardMenu = ({
  onClose,
  menuData,
  onRefetch,
  qid,
}) => {
  const { t } = useTranslation();
  const [disabled, setDisabled] = useState(false);
  const [edit, setEdit] = useState(false);
  const [committeeAuthorityRemove] = useCommitteeAuthorityRemove();
  const [committeeAuthorityUpdate] = useCommitteeAuthorityUpdate();

  const onDelete = () => {
    if (menuData?._id && qid) {
      setDisabled((pre) => !pre);
      committeeAuthorityRemove({
        qid: qid,
        caid: menuData?._id,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  const onOpenEdit = () => {
    setEdit((pre) => !pre);
  };
  const onEdit = (val) => {
    if (menuData?._id) {
      onOpenEdit();
      setDisabled((pre) => !pre);
      committeeAuthorityUpdate({
        caid: menuData?._id,
        bodyParameter: val,
      })
        .then(() => {
          onRefetch();
          setDisabled((pre) => !pre);
          onClose();
        })
        .catch({});
    }
  };

  // console.info("menuData", menuData);
  return edit ? (
    <AddCommitteeAuthority
      openAs="EDIT"
      onClose={onOpenEdit}
      onEdit={onEdit}
      menuData={menuData}
    />
  ) : (
    <ModalMenu onClose={onClose} disabled={disabled}>
      <h6 onClick={onOpenEdit}>{t("edit")}</h6>
      <h6 onClick={onDelete}>{t("delete")}</h6>
    </ModalMenu>
  );
};

export default InstituteCommitteeAutorityCardMenu;
